<template>

  <div>
    <q-banner dense class="bg-primary text-white">
      <template v-slot:avatar>
        <q-icon name="signal_wifi_off"/>
      </template>
      Es besteht keine Verbindung zum Proberaum. Bitte überprüfe die Verbindung zum Proberaum-WLAN.
      <template v-slot:action>
        <q-btn flat no-caps label='Erneute versuchen' :loading='isLoading' icon='refresh' color="white" @click="retry">
            <template v-slot:loading>
              <q-spinner class="on-left" />
            </template>
        </q-btn>
      </template>
    </q-banner>


  </div>
</template>

<script>
import { useInstrumentStore } from 'stores/instruments-store';

export default {
  data() {
    return {
      isLoading: false,
    };
  },
  setup() {
    const store = useInstrumentStore();
  },
  computed: {
    isAvailable() {
      const store = useInstrumentStore();
      return store.isBackendAvailable;
    }
  },
  methods:{
    retry() {
      const store = useInstrumentStore();
      this.isLoading = true;

      store.checkBackendAvailability();

      setTimeout(() => {
        this.isLoading = false;
      }, 1500)

    },
  }
};
</script>